<template>
  <div>
    <div class="module filter">
      <div class="l">
        <el-input placeholder="请输入内容" v-model="form.keyword">
          <template slot="prepend">搜索主体</template>
        </el-input>
      </div>

      <div class="l">
        <el-button @click="getList()">搜索</el-button>
      </div>
    </div>

    <div class="module">
      <div class="l">
        <el-button @click="addDialog('新增')">新增</el-button>
      </div>
    </div>

    <div class="module">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="id" label="系统ID" width="100" align="center"> </el-table-column>

        <el-table-column prop="remarksname" label="证书备注名" width="200">
        </el-table-column>
        <el-table-column prop="accesskey" label="accesskey">
        </el-table-column>

        <el-table-column prop="create_time" label="创建时间" width="200">
        </el-table-column>
        <el-table-column prop="status" label="状态" width="70">
          <template slot-scope="scope">
            <span v-show="scope.row.status === 0">启用</span>
            <span v-show="scope.row.status === 1">禁用</span>
          </template>
        </el-table-column>

        <el-table-column label="更多" fixed="right" width="125" align="center">
          <template slot-scope="scope">
            <el-dropdown>
              <el-button type="primary">
                管理<i class="el-icon-arrow-down el-icon--right"></i>
              </el-button>
              <el-dropdown-menu slot="dropdown">
                <!-- <router-link :to="{path:'/iot/equipment/params', query:{rowId1: updateTable.gateway_id, rowId2: updateTable.id,}}">
                  <el-dropdown-item @click.native="detail(scope.row)">查看</el-dropdown-item>
                </router-link> -->
                <el-dropdown-item @click.native="detail(scope.row)">查看</el-dropdown-item>
                <el-dropdown-item @click.native="updatedia(scope.row, '修改')">修改</el-dropdown-item>
                <el-dropdown-item @click.native="dele()">删除</el-dropdown-item>
                <el-dropdown-item v-show="scope.row.status" @click.native="useif(scope.row)">启用
                </el-dropdown-item>
                <el-dropdown-item v-show="!scope.row.status" @click.native="useif(scope.row)">禁用
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>

      </el-table>

      <el-dialog :title="upType == '新增' ?'新增证书' : '修改证书'" :visible.sync="dialogVisible" width="30%" :show-close='false'
        :close-on-click-modal='false'>
        <div class="module">
          <div class="mtitle">证书备注名</div>
          <el-input v-model="updateTable.remarksname" placeholder="证书备注名"></el-input>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click.native="updateList()">确 定</el-button>
        </span>
      </el-dialog>
    </div>

    <div class="module">
      <Pagination @pagination="getList()" :page.sync="form.querypage" :pageCount.sync="totalPage" :total.sync="total"
        v-if="total > 0" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { All, Create, Change } from "@/api/opendata";
import Pagination from "@/components/Pagination.vue";
export default {
  name: "opendataIndex",
  data() {
    return {
      form: {
        keyword: "",
        querypage: 1,
      },
      tableData: [], // 列表
      total: 0, // 总条目
      totalPage: 0, // 总页数

      rowId: "",

      dialogVisible: false,
      updateTable: [],
      upType: "",
    };
  },
  components: {
    Pagination,
  },
  methods: {
    getList() {
      this.$http(All(this.form), (res) => {
        if (res.code == 200) {
          this.$message({
            message: res.msg,
            type: "success",
          });
          this.total = res.data.total;
          this.form.querypage = res.data.currentPage;
          this.totalPage = res.data.totalPages;
          this.tableData = res.data.result;
        } else {
          this.$message({
            message: res.msg,
            type: "error",
            duration: 5 * 1000,
          });
        }
      });
    },
    addDialog(type) {
      this.upType = type;
      this.updateTable = {};
      this.dialogVisible = true;
    },
    updatedia(item, type) {
      this.upType = type;
      this.dialogVisible = true;
      this.updateTable = item;
    },
    dele() {
      this.$confirm(
        "状态",
        "这是一段内容确定删除吗？",
        () => {
          //删除接口
          // this.$http(
          //   Managermainspecies({
          //     id: id,
          //     set: 1
          //   }),
          //   res => {
          //     if (res.code == 200) {
          //       this.$message({
          //         message: res.msg,
          //         type: "success"
          //       });
          //       this.getList();
          //     } else {
          //       this.$message({
          //         message: res.msg,
          //         type: "error",
          //         duration: 5 * 1000
          //       });
          //     }
          //   }
          // );
        },
        () => {
          this.$message("已取消");
        }
      );
    },
    useif(item) {
      this.updateTable = item;
      console.log(this.updateTable);
      const name = item.status ? "启用" : "禁用";

      this.$confirm(
        "启用/禁用",
        `是否确定${name}`,
        () => {
          this.$http(
            Change({
              id: this.updateTable.id,
              set: 1,
            }),
            (res) => {
              if (res.code == 200) {
                this.getList();
              } else {
                this.$message({
                  message: res.msg,
                  type: "error",
                  duration: 5 * 1000,
                });
              }
            }
          );
        },
        () => {
          this.$message("已取消");
        }
      );
    },
    detail(item) {
      this.updateTable = item;
    },
    updateList() {
      if (this.upType == "新增") {
        this.$http(
          Create({
            remarksname: this.updateTable.remarksname,
          }),
          (res) => {
            if (res.code == 200) {
              this.$message({
                message: res.msg,
                type: "success",
              });
              this.getList();
              this.dialogVisible = false;
            } else {
              this.$message({
                message: res.msg,
                type: "error",
                duration: 5 * 1000,
              });
            }
          }
        );
      } else {
        this.$http(
          Create({
            id: this.updateTable.id,
            remarksname: this.updateTable.remarksname,
          }),
          (res) => {
            if (res.code == 200) {
              this.getList();
              this.dialogVisible = false;
            } else {
              this.$message({
                message: res.msg,
                type: "error",
                duration: 5 * 1000,
              });
            }
          }
        );
      }
    },
  },
  created() {
    this.rowId = this.$route.query.rowId || "";
    this.getList();
  },
};
</script>
<style lang="scss" scoped>
</style>