import request from '@/utils/request'

export function All(data) {
    return request({
        url: '/accesskey/all',
        method: 'post',
        data: data
    })
}

export function Create(data) {
    return request({
        url: '/accesskey/create',
        method: 'post',
        data: data
    })
}

export function Change(data) {
    return request({
        url: '/accesskey/change',
        method: 'post',
        data: data
    })
}